}
.btnSwitch {
  position: absolute;
  left: 70px;
  top: 50px;
  width: 404px;
  height: 108px;
  color: #fff;
  background: url('/images/imgs/03图层管理/块.png');
  text-align: center;
  line-height: 108px;
  font-size: 44px;
  cursor: pointer;
}
.btnSwitch .kai {
  width: 61px;
  height: 61px;
  background: url('/images/imgs/03图层管理/显示管网.png');
  display: inline-block;
  position: absolute;
  top: 25px;
  left: 35px;
}
.btnSwitch.btnSwitch_active .kai {
  width: 61px;
  height: 61px;
  background: url('/images/imgs/03图层管理/关闭管网.png');
  display: inline-block;
  position: absolute;
  top: 25px;
  left: 35px;
}
.btnSwitch:hover {
  background: url('/images/imgs/03图层管理/块-滑过色.png');
}
