/******************************第一屏弹出窗口*********************************************/
.po {
  width: 115%;
  border-style: solid;
  border-color: transparent;
  border-width: 405px 310px 60px 165px;
  border-image: url("../images/科技tip框.png");
  border-image-slice: 405 310 60 165 fill;
  padding: 0;
}
.po > div {
  height: 110%;
  background-color: #000f4c;
  opacity: 0.7;
  margin-top: -330px;
  margin-left: -133px;
  margin-right: -305px;
  margin-bottom: -25px;
  font-family: MicrosoftYaHei;
  font-size: 30px;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 2px;
  line-height: 60px;
  color: #ffffff;
  padding: 0 48px;
  border: 0.1px solid #ffffff;
  border-radius: 3% 5% 1% 30%;
  white-space: nowrap;
}
.po_name {
  width: 206px;
  height: 46px;
  text-align: center;
  line-height: 100%;
  border-bottom: 3px solid #0090ff;
  margin-top: -29px;
}
.po_name > div {
  margin-right: 18px;
}
.po_plan {
  max-height: 600px;
  margin: 40px 0px;
  overflow-y: auto;
}
.po_plan_basic {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.po_plan_basic_keys {
  flex: 0.3;
}
.po_plan_basic_vals {
  flex: 1;
}
.po_plan_stat {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin: 20px 0px;
}
.po_plan_stat_keys {
  flex: 0.3;
  line-height: 60px;
}
.po_plan_stat_vals {
  flex: 1;
}
.po_plan_stat_vals_zt {
  display: flex;
  flex-direction: column;
}
.po_plan_realTime {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.po_plan_realTime_data {
  width: 44%;
  display: flex;
  flex-direction: column;
}
.po_plan_realTime_data_xs {
  display: flex;
  justify-content: space-between;
  width: 115%;
}
.po_plan_realTime_data_xs_keys {
  flex: 0.3;
}
.po_plan_realTime_data_xs_vals {
  width: 230px;
  display: flex;
  font-family: "webfont" !important;
  font-size: 46px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0px;
  color: #00ffff;
}
.po_plan_realTime_data_xs_vals > span {
  font-family: MicrosoftYaHei;
  font-size: 26px;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 1px;
  color: #00ffff;
  margin-left: 16px;
}
.po_plan_realTime_imgs {
  margin-left: 30px;
  width: 382px;
  height: 205px;
  background-color: #001b5c;
}
.po_plan_realTime_imgs > img {
  width: 382px;
  height: 190px;
}
.po_plan::-webkit-scrollbar {
  /*滚动条整体样式*/
  width: 20px;
  /*高宽分别对应横竖滚动条的尺寸*/
  height: 1px;
}
.po_plan::-webkit-scrollbar-thumb {
  /*滚动条里面小方块*/
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 5px #000000;
  background: #0090ff;
}
.po_plan::-webkit-scrollbar-track {
  /*滚动条里面轨道*/
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  background: rgba(2, 105, 199, 0.1);
}
.cal {
  background: url(../images/关闭.png);
  width: 37px;
  height: 37px;
  position: absolute;
  right: 75px;
  top: 10px;
  cursor: pointer;
}
.bjzt {
  width: 610px;
  height: 74px;
  display: flex;
  flex-direction: column;
}
.bjzt > img {
  width: 36px;
  height: 46px;
}
.bjzt > i {
  width: 29px;
  height: 20px;
  font-family: MicrosoftYaHei;
  font-size: 24px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 40px;
  letter-spacing: 1px;
  color: #0090ff;
}
/******************************第三屏视频窗口*********************************************/
.vi {
  width: 680px;
  height: 860px;
  background-color: rgba(14, 70, 152, 0.5);
  border: solid 1px #00ffff;
  border-radius: 40px;
}
.vi_gb {
  width: 90%;
  text-align: end;
  font-size: 46px;
  color: #00ffff;
  cursor: pointer;
}
.vi_plan {
  width: 90%;
  padding: 10px 20px;
  max-height: 724px;
  overflow-y: auto;
}
.vi_plan > div {
  font-family: MicrosoftYaHei;
  font-size: 46px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0px;
}
.vi_plan_x {
  cursor: pointer;
  color: #fff;
  margin: 35px;
}
.vi_plan_x:hover {
  color: #00ffff;
}
.vi_plan::-webkit-scrollbar {
  /*滚动条整体样式*/
  width: 20px;
  /*高宽分别对应横竖滚动条的尺寸*/
  height: 1px;
}
.vi_plan::-webkit-scrollbar-thumb {
  /*滚动条里面小方块*/
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 5px #000000;
  background: #00ffff;
}
.vi_plan::-webkit-scrollbar-track {
  /*滚动条里面轨道*/
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  background: rgba(2, 105, 199, 0.1);
}
